  // Carousel navigation
  function updateCarousel() {
    // Get actual card dimensions from DOM to avoid hardcoding
    const firstCard = pricingCards.querySelector('.pricing-card');
    if (!firstCard) return;

    const cardStyles = getComputedStyle(firstCard);
    const cardWidth = firstCard.offsetWidth;
    const cardMarginRight = parseInt(cardStyles.marginRight) || 0;
    const gap = cardMarginRight;

    const translateX = currentSlide * (cardWidth + gap);
    pricingCards.style.transform = `translateX(-${translateX}px)`;

    // Update button states
    prevBtn.disabled = currentSlide === 0;
    nextBtn.disabled = currentSlide >= maxSlide;
  }

  // Unified carousel navigation function
  function navigateCarousel(direction) {
    const newSlide = currentSlide + direction;
    if (newSlide >= 0 && newSlide <= maxSlide) {
      currentSlide = newSlide;
      updateCarousel();
    }
  }

